import React, { useState } from 'react';
import Markdown from './Markdown';
import MarkdownPreview from './MarkdownPreview';
import { demotext } from '../data/demoText';

function MarkdownApp() {
  const [markdown, setMarkdown] = useState({
    isRealData: false,
    text: demotext,
  });

  const clickHandler = () =>
    markdown.isRealData
      ? setMarkdown({
          isRealData: false,
          text: demotext,
        })
      : setMarkdown({
          isRealData: true,
          text: '### Amr Saad',
        });

  return (
    <div className="container">
      <button
        className={`btn ${
          markdown.isRealData ? 'btn-primary' : 'btn-secondary'
        } bg-gradient d-block mx-auto mb-5`}
        type="button"
        onClick={clickHandler}
      >
        {markdown.isRealData ? 'Show Demo' : 'Clear MarkDown Board'}
      </button>

      <div className="row row-cols-1 row-cols-lg-2 g-4">
        <div className="col">
          <Markdown
            markdownContent={markdown.text}
            onMarkdownChange={setMarkdown}
          />
        </div>

        <div className="col">
          <MarkdownPreview markdownContent={markdown.text} />
        </div>
      </div>
    </div>
  );
}

export default MarkdownApp;
