import React from 'react';
import PropTypes from 'prop-types';

function Markdown({ markdownContent, onMarkdownChange }) {
  return (
    <div className="markdown rounded bg-white border shadow-sm p-2  h-100">
      <textarea
        className="w-100 p-2 d-block h-100"
        style={{ resize: 'none' }}
        name="markdown"
        value={markdownContent}
        onChange={(e) =>
          onMarkdownChange({
            isRealData: true,
            text: e.target.value,
          })
        }
      />
    </div>
  );
}

Markdown.propTypes = {
  markdownContent: PropTypes.string,
  onMarkdownChange: PropTypes.func,
};

export default Markdown;
